.Nav {
    height: 100%;
    background-color: var(--dark-bg);
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: larger;
    padding: 0 2rem;
    font-size: 1.3rem;
    font-weight: bold;
    box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 36px 0px, rgba(0, 0, 0, 0.06) 0px 0px 0px 1px;

    &>* {
        margin: 0 15px;
    }
    a {
        margin: 0 1.8rem;
        color: var(--font);
        transition: color 0.5s ease;
    }

    a:hover {
        color: var(--white);
    }

    @media (max-width: 768px) {
        font-size: medium;
        justify-content: center;


        a {
            margin: 0 1.5rem;

        }

        img {
            width: auto;
            height: 40px;
            margin-right: 10px;
        }

        span {
            display: none;
        }
    }
}