.About {
    .aboutPara {
        margin: 0 auto;
        padding: 10vh 40vh 0 40vh;

        h1 {
            font-size: xx-large;
        }

        p {
            font-size: x-large;
            margin: 0;
        }

    }

    .aboutData {
        display: flex;
        justify-content: space-between;
        margin: 0 10vh;


        .aboutSection {
            width: 45%;

            h4 {
                font-size: xx-large;
            }

            .aboutContentDetails {
                font-size: 1.3rem;
                color: #666;
                max-width: 60rem;
                line-height: 1.7;
                margin-bottom: 1rem;
                text-align: start;

                a {
                    padding: 0 5px;
                    color: var(--black);
                    font-weight: bold;
                }
            }

            .btn-p {
                padding: 0.4em 2em;
                margin: 1em;
            }
        }
    }

    @media (max-width: 768px) {
        .aboutPara {
            margin: 0 auto;
            padding: 0 55vh;
        }

        .aboutData {
            padding: 0 25vh;
            flex-direction: column;
            align-items: center;

            .aboutSection {
                .aboutContentDetails {
                    // text-align: center;
                }
            }
        }
    }
}